.sign-up-btn {
  padding: 12px 22px;
  background-color: #00A870;
  color: #ffffff;
  border-radius: 4px;
  height: 44px;
  cursor: pointer;

  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

@media screen and (max-width: 600px) {
  .sign-up-btn {
    width: 240px;
  }
}